/*
highlight.js style
Monokai style - ported by Luigi Maselli - http://grigio.org
*/
pre {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #272822; color: #ddd;
}
.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #272822; color: #ddd;
}

.hljs-tag,
.hljs-keyword,
.hljs-selector-tag,
.hljs-literal,
.hljs-strong,
.hljs-name {
  color: #f92672;
}

.hljs-code {
  color: #66d9ef;
}

.hljs-class .hljs-title {
  color: white;
}

.hljs-attribute,
.hljs-symbol,
.hljs-regexp,
.hljs-link {
  color: #bf79db;
}

.hljs-string,
.hljs-bullet,
.hljs-subst,
.hljs-title,
.hljs-section,
.hljs-emphasis,
.hljs-type,
.hljs-built_in,
.hljs-builtin-name,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-addition,
.hljs-variable,
.hljs-template-tag,
.hljs-template-variable {
  color: #a6e22e;
}

.hljs-comment,
.hljs-quote,
.hljs-deletion,
.hljs-meta {
  color: #75715e;
}

.hljs-keyword,
.hljs-selector-tag,
.hljs-literal,
.hljs-doctag,
.hljs-title,
.hljs-section,
.hljs-type,
.hljs-selector-id {
  font-weight: bold;
}


/**
 *
 * Mardown Style
 *
 */

table, td, th {
  border: 1px solid;
  padding: 5px 10px;
}

table {
  border-spacing: 0px;
  border-collapse: collapse;
}


/**
 *
 * Main Css
 *
 */


* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  background: #74ebd5;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #ACB6E5, #74ebd5);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#ACB6E5), to(#74ebd5));
  background: -webkit-linear-gradient(right, #ACB6E5, #74ebd5);
  background: -o-linear-gradient(right, #ACB6E5, #74ebd5);
  background: linear-gradient(to left, #ACB6E5, #74ebd5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #444;
  font-family: 'Helvetica Neue', sans-serif;
}

@media (min-width: 768px) {
  .container {
    margin: auto;
    padding: 0;
    max-width: 900px;
  }
}
.container {
  padding: 50px 20px;
}


/**
 *
 * Title bar
 *
 */

.title-bar {
   cursor: pointer;
}


/**
 *
 * Editor
 *
 */

.editorWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 2px 2px hsla(0, 0%, 50%, 0.1),
              0 0 6px 6px hsla(0, 0%, 50%, 0.1);
          box-shadow: 0 0 2px 2px hsla(0, 0%, 50%, 0.1),
              0 0 6px 6px hsla(0, 0%, 50%, 0.1);
  background: white;
  margin-bottom: 50px;
}

#editor {
  margin: auto;
  padding: 20px;
  width: 100%;
  height: 200px;
  font-size: 14px;
  resize: vertical;
  border: none;
  background: none;
}

#editor:focus {
  outline: none;
}


/**
 *
 * Previewer
 *
 */

.previewerWrapper {
  margin-bottom: 50px;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 2px 2px hsla(0, 0%, 50%, 0.1),
              0 0 6px 6px hsla(0, 0%, 50%, 0.1);
          box-shadow: 0 0 2px 2px hsla(0, 0%, 50%, 0.1),
              0 0 6px 6px hsla(0, 0%, 50%, 0.1);
  background: white;
  overflow: hidden;
}

#preview {
  padding: 20px;
  overflow: auto;
  height: 100%;
}


/**
 *
 * Maximized style
 *
 */

.maximized {
  position: fixed;
  margin: 0;
  padding: 0;
  top: 5vmin;
  left: 5vmin;
  width: calc(100vw - 10vmin);
  height: calc(100vh - 10vmin);
  z-index: 1;
  -webkit-box-shadow: 0 0 1px 1px hsla(0, 0%, 50%, 0.1),
              0 0 2px 2px hsla(0, 0%, 50%, 0.2),
              0 0 6px 6px hsla(0, 0%, 50%, 0.2),
              0 0 12px 12px hsla(0, 0%, 50%, 0.2);
          box-shadow: 0 0 1px 1px hsla(0, 0%, 50%, 0.1),
              0 0 2px 2px hsla(0, 0%, 50%, 0.2),
              0 0 6px 6px hsla(0, 0%, 50%, 0.2),
              0 0 12px 12px hsla(0, 0%, 50%, 0.2);
}

.maximized #editor {
  height: 100%;
  resize: none;
}

/**
 *
 * Others
 *
 */

img {
  max-width: 100%;
}


